<template>
    <v-container style="width: 100%; " align="center" fluid>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                dense
        >
            <v-row>
                <!--<v-col></v-col>-->
                <v-col class="text-center pa-0">
                    <v-toolbar-title>{{eventsText}}</v-toolbar-title>
                    <!--<v-btn  color="primary" @click="getMessagingToken">
                        <p>uwu2</p>
                    </v-btn>-->
                </v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>
            <template class="pa-0" v-slot:extension style="width: 100%!important">
                <v-tabs v-model="eventsTabs">
                    <v-tab key="list" style="width: 50vw!important; min-width: 50vw!important;">
                        <v-icon>mdi-card-text</v-icon>
                    </v-tab>
                    <v-tab key="map" style="width: 50vw!important; min-width: 50vw!important;">
                        <v-icon>mdi-map</v-icon>
                    </v-tab>
                </v-tabs>
            </template>
        </v-app-bar>

        <v-tabs-items v-model="eventsTabs" style="background: transparent">
            <v-tab-item key="list" style="margin-top: 94px; background: transparent">
                <v-row class="pa-0" dense style="padding-left: 12px; padding-right: 12px">
                    <v-select
                            v-model="languageInvolvedFilter"
                            style="margin-top: 15px; padding-bottom: 0px!important;"
                            :items="languages"
                            item-value="id"
                            item-text="option"
                            solo
                            dense
                            return-object
                    >

                        <template v-slot:item="{item}">
                            <v-avatar>
                                <img :src="createPathToFlagImage(item.id)"
                                     style="height: 60%; width: auto"/>
                            </v-avatar>
                            {{ item.text }}
                        </template>
                        <template v-slot:selection="{item, select, selected }">
                            <v-avatar>
                                <img :src="createPathToFlagImage(item.id)"
                                     style="height: 60%; width: auto"/>
                            </v-avatar>
                            {{ item.text }}
                        </template>
                    </v-select>
                </v-row>
                <v-row v-if="events.length > 0" dense style="padding-left: 12px; padding-right: 12px; ">
                    <div
                            style="width: 100%"
                            v-for="event in events"
                            :key="event.id"
                    >
                        <v-card
                                class="mx-auto"
                                max-width="400"
                                min-width="280"
                                @click="openEventDescription(event.id)"
                        >
                            <v-img
                                    class="white--text align-end"
                                    height="150px"
                                    :src="event.headerImg"
                                    style="position: relative"
                            >

                                <v-chip
                                        v-if="userLocation != null && event.distance != null"
                                        style="display:inline; float:right; margin-right: 5px; bottom: 5px"
                                        color="primary"
                                        small
                                >
                                    <p class="text-with-icon ma-1">
                                        <v-icon style="display: inline; vertical-align: top; font-size:18px;">
                                            mdi-map-marker-outline
                                        </v-icon>
                                        {{event.distance.toFixed(2)}} km
                                    </p>
                                </v-chip>
                            </v-img>

                            <v-card-text class="text--primary" style="padding-bottom: 2px!important;">

                                <div style="display: flex; position: relative;">
                                    <div style="margin-top: 10.5px">
                                        <strong style="font-size: 46px;">{{getDay(event.date)}}</strong>
                                    </div>
                                    <div style="margin-left: 5px">
                                        <div>
                                            <strong>{{getMonth(event.date)}}</strong>
                                        </div>
                                        <div>
                                            <strong>{{getYear(event.date)}}</strong>
                                        </div>
                                    </div>
                                    <div style="margin-left: 25px">
                                        <div>
                                            <strong style="font-size: 18px; margin-bottom: 0px">"{{event.name}}"</strong>
                                        </div>
                                        <div>
                                            <p class="text-with-icon">
                                                <v-icon style="display: inline;  vertical-align: top; font-size:18px;">
                                                    mdi-store
                                                </v-icon>
                                                {{event.location.place}} ({{event.location.city}})
                                            </p>
                                        </div>
                                    </div>
                                </div>
                                <v-spacer></v-spacer>
                            </v-card-text>
                        </v-card>
                        <br>
                    </div>
                </v-row>

                <v-row>
                    <v-col style="padding-top: 0px!important">
                        <v-container class="pa-0">
                            <v-btn color="primary" @click="listEvents(false)" v-if="!buttonIsDisabled"
                                   :disabled="buttonIsDisabled"
                                   class="bottom-centered-buttons">
                                {{watchMoreButtonText}}
                            </v-btn>
                        </v-container>
                    </v-col>
                </v-row>

                <v-card v-if="events.length == 0 && eventsListed" class="pa-2 ma-1"
                        style="height: calc(100vh - 276px); margin-top:-16px!important;">
                    <v-row class="text-center justify-center">
                        <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noEventsText}}</p>
                    </v-row>
                    <v-row class="text-center justify-center">
                        <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
                    </v-row>
                </v-card>
            </v-tab-item>
            <v-tab-item key="map" style="background: transparent">
                <v-card v-if="userLocation != null" style="top: 110px">
                    <v-card-actions class="pa-0 justify-center">
                        <GmapMap
                                class="ma-2"
                                :center="userLocation"
                                :zoom="14"
                                style="width:98%;  height: calc(100vh - 228px); min-height: 240px"
                        >
                            <GmapMarker
                                    :position="userLocation"
                                    icon="https://lh3.googleusercontent.com/0yEYKAE8OV71nwMPSXRaJWXYlrlS9egpqzHpfO60Z0GPMeMSheytZfo6_ZwmTDlmyh4zxff6I6dO5CNmJp-66w2fZeyPEmmkCRMulyMQTseDqq1VfwsYRR42AlAsw7Vhmh-rrJd-dYufBG48WrcfSubgdVdqQfXxWzNNNynL1Jf8PbQLaRwpLunQO7NERQTcGVEcXZOVyYB6Ihj450LkI6lYzrmFbP0JQdlf7xB7QtepPgdCrrnOF1Oe8WL6gAhX6bWd4HHcahbqA9G9e2XNchgMS2uwQW21_oDiVBw7L1EcJywhgY8cRtFD2ims4qly7Av6yw3oNjD8MZxQ5AWTQzZsOSqV24XIorr3a1b-MJ-a6RdPwq5nm3nZV7G6zRzS_KPiCcLtPs5P266W5DMQRWd1R9NLroLbklnwdrxFgg5OTq33DAZxgR8ECT5n6S8TzDovZG0rZEMAVOB2gyGvV8D5JIf6-BRdKrLC72Sf9mHAiVHXUzYohBpDVxfDGHnVeFeTINBe4VASMDE_WUdVuwiuYlOQpOmbUkd7cGvc2c6J0tJANYULPXGfu-H1ZN3STX7TXXyDgHxBDzc4PMmkcUBs4mD91F6Lz4wPztfwxHcEI7Ycz4PdrCqI4d0hxA-aK-zXht3CLhagGENS59O2V_iFR3A6YL4brn9rVm_8rv0t3v2U1GIBLSZa-Age=w30-h41-no"
                            />
                            <GmapMarker
                                    v-for="event in mapEvents"
                                    :key="event.id"
                                    :position="event.location.coordinates"
                                    :clickable="true"
                                    @click="openEventDescription(event.id)"
                            />
                        </GmapMap>
                    </v-card-actions>
                </v-card>

                <v-card v-if="userLocation == null" class="pa-2 ma-1"
                        style="height: calc(100vh - 228px); top: 110px">
                    <v-row class="text-center justify-center">
                        <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noLocationPermText}}</p>
                    </v-row>
                    <v-row class="text-center justify-center">
                        <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
                    </v-row>
                </v-card>
            </v-tab-item>
        </v-tabs-items>
    </v-container>
</template>

<script>
    import {mapActions, mapState, mapMutations, mapGetters} from 'vuex';
    import Constants from "../../store/Constants";

    import axios from 'axios';
    import * as firebase from "firebase";

    export default {
        name: "EventsHome",
        data() {
            return {
                // default to Montreal to keep it simple
                // change this to whatever makes sense
                watchMoreButtonText: Constants.TEXT_WATCH_MORE,
                buttonIsDisabled: true,
                center: {lat: 45.508, lng: -73.587},
                markers: [],
                places: [],
                currentPlace: null,
                eventsText: Constants.TEXT_EVENTS,
                noEventsText: Constants.TEXT_NO_EVENTS_AVAILABLE,
                sadFaceImg: Constants.IMG_EMOJI_SAD_FACE,
                eventsListed: false,
                languageInvolvedFilter: null,
                languages: null,
                eventLanguageText: Constants.TEXT_LANGUAGE_INVOLVED_IN_EVENTS,
                youText: Constants.TEXT_YOU,
                eventsTabs: null,
                mapEventsListed: false,
                noLocationPermText: Constants.TEXT_LOCATION_PERMISSION_NOT_GRANTED
            };
        },

        methods: {
            ...mapActions('events', ['getEvents', 'resetState']),
            ...mapMutations('events', ['setLanguageInvolvedFilter', 'resetEventsToShowIDs']),

            getMonth(date) {
                return this.$store.getters.getFormattedMonthIn3CapitalLettersText(date.toDate());
            },
            getDay(date) {
                return date.toDate().getDate();
            },
            getYear(date) {
                return date.toDate().getFullYear();
            },
            listEvents: function () {
                let self = this;
                self.buttonIsDisabled = true;
                let resultGetEvents = self.getEvents();

                resultGetEvents.then((moreEventsToShow) => {
                    self.buttonIsDisabled = !moreEventsToShow;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            createPathToFlagImage: function (lang) {
                return "../img/flags/" + lang + ".svg";
            },
            // receives a place object via the autocomplete component
            setPlace(place) {
                this.currentPlace = place;
            },
            addMarker() {
                if (this.currentPlace) {
                    const marker = {
                        lat: this.currentPlace.geometry.location.lat(),
                        lng: this.currentPlace.geometry.location.lng()
                    };
                    this.markers.push({position: marker});
                    this.places.push(this.currentPlace);
                    this.center = marker;
                    this.currentPlace = null;
                }
            },
            openEventDescription(eventID) {
                let self = this;

                this.$router.push('event/' + eventID);
            },
        },
        computed: {
            ...mapGetters('events', {events: 'getEventsToShow'}),
            ...mapGetters('events', {mapEvents: 'getMapEventsToShow'}),
            userLocation: function () {
                return this.$store.getters['user/getLocation'];
            }
        },
        beforeMount() {
            let self = this;
            self.languages = self.$store.getters['user/getLanguageOptionsAsArray'];
        },

        mounted() {
            let self = this;
            self.resetEventsToShowIDs();
            self.eventsTabs = self.$store.getters['events/getEventsTab'];

            self.eventsListed = false;
            if (self.events.length == 0) {
                self.getEvents().then((moreEventsToShow) => {
                    self.buttonIsDisabled = !moreEventsToShow;
                    self.eventsListed = true;
                    self.languageInvolvedFilter = self.$store.getters['events/getLanguageInvolvedFilter'];
                });
            }

        },
        watch: {
            mapEvents: function () {
                if (this.mapEvents.length > 0) {
                    self.mapEventsListed = true;
                }
            },
            eventsTabs: function () {
                this.$store.commit('events/setEventsTab', this.eventsTabs);
            },
            languageInvolvedFilter: function (newLanguage, oldLanguage) {
                let selectedInvolvedLanguage;
                let oldSelectedInvolvedLanguage;

                if (oldLanguage != null && oldLanguage.hasOwnProperty("id")) {
                    oldSelectedInvolvedLanguage = oldLanguage.id;
                } else {
                    oldSelectedInvolvedLanguage = oldLanguage;
                }

                if (newLanguage.hasOwnProperty("id")) {
                    selectedInvolvedLanguage = newLanguage.id;
                } else {
                    selectedInvolvedLanguage = newLanguage;
                }

                let self = this;

                if (oldSelectedInvolvedLanguage != null && oldSelectedInvolvedLanguage != selectedInvolvedLanguage) {
                    self.resetEventsToShowIDs();
                    self.eventsListed = false;
                    self.setLanguageInvolvedFilter(selectedInvolvedLanguage);

                    self.getEvents().then((moreEventsToShow) => {
                        self.buttonIsDisabled = !moreEventsToShow;
                        self.eventsListed = true;
                    });
                }
            },

        },
    }
</script>

<style scoped>
    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }
</style>